    justify-content: space-between;
    align-items: center;
  }
  .testimonials-filters__button {
    width: 324px;
    display: block;
    grid-column: 1/2;
    padding-top: 18px;
    padding-bottom: 12px;
  }
  .testimonials-filters__marked {
    font-size: 14px;
    display: block;
    grid-column: 2/3;
  }
}
@media (min-width: 1200px) {
  .testimonials-filters {
    grid-template-columns: 493px 467px;
  }
  .radio-button {
    display: grid;
    grid-template-columns: repeat(3, 262px);
    justify-content: center;
    column-gap: 43px;
  }
  .landmark {
    margin-top: 14px;
    margin-bottom: 43px;
  }
  .checkbox {
    display: grid;
    grid-template-columns: repeat(4, 185px);
    justify-content: center;
    column-gap: 43px;
  }
  .textarea__text {
    width: 77%;
    margin: 0 auto;
  }
  .testimonials-filters__button {
    width: 384px;
    margin-bottom: 5px;
  }
  .form-button {
    width: 80%;
    margin: 0 auto;
  }
}
